.popup__header {
    font-family: Inter, Arial, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    margin: 34px 64px 54px 36px;
}

@media screen and (max-width:645px) {
    .popup__header {
        font-size: 18px;
        line-height: 22px;
        min-width: 238px;
        margin: 25px 22px 75px;
    }
}